// #region Local Imports
import { Http } from "@Services";
// #endregion Local Imports

// #region Interface Imports
import { PostModel, HashTagModel } from "@Interfaces";
// #endregion Interface Imports

export const PostService = {
    GetHashTag: async (
        payload: HashTagModel.GetHashTagPayload
    ): Promise<HashTagModel.GetHashTagResponse> => {
        const response = await Http.Request<HashTagModel.GetHashTagResponse>(
            "GET",
            "/hash-tag",
            payload.params
        );

        return response;
    },
    GetPost: async (
        payload: PostModel.GetPostPayload
    ): Promise<PostModel.GetPostResponse> => {
        const response = await Http.Request<PostModel.GetPostResponse>(
            "GET",
            "/post",
            payload.params
        );

        return response;
    },
};
